<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>修改密码</span>
      </div>

      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="原密码" prop="age">
          <el-input class="userinput" type="password" v-model.number="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input class="userinput" type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input
            class="userinput"
            type="password"
            v-model="ruleForm.checkPass"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// 引入接口API
import { API_CHECK_PWD } from "@/API/apis.js";
import { API_EDIT_PWD } from "@/API/apis.js";
export default {
  data() {
    // 创建密码验证规则
    var checkAge = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    // 密码一致验证
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      // 表单输入数据
      ruleForm: {
        pass: "",//新密码
        checkPass: "",//检查密码是否一致
        age: "" //原密码
      },
      rules: { //使用验证规则
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        age: [{ validator: checkAge, trigger: "blur" }]
      },

      labelPosition: "left" //label位置 左
    };
  },
  methods: {
    // 提交表单
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //console.log(localStorage.getItem("id"))
          // 获取到当前登录的用户ID
          localStorage.getItem("id");
          //this.ruleForm.age 原密码
          let userID = localStorage.getItem("id"); //定义变量接收用户id
          API_CHECK_PWD(this.ruleForm.age, userID).then(res => {
            // console.log(res.data)
            if (res.data.code == 1) {
              this.$message.error(res.data.msg);
              this.ruleForm.age = ""; //直接清空原密码，防止用户重复点击
            } else {
              //原密码正确，发送请求，修改密码
              // 获取新密码
              let newPwd = this.ruleForm.pass;
              API_EDIT_PWD(newPwd, userID).then(res => {
                // console.log(res)
                if (res.data.code == 0) {
                  this.$message({  //消息弹框 
                    message: res.data.msg,
                    type: "success"
                  });
                  setTimeout(()=>{ //定时跳转到登录页
                    localStorage.removeItem("id")
                    localStorage.removeItem("token")
                    location.href='/'; 
                  },1500)
                }
              });
            }
          });
        } else {
          screenTop;
          console.log("error submit!!");  //修改密码错误提示
          return false;
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
.userinput {
  width: 200px;
}
</style>